import fetchRetry from "fetch-retry-ts";
import {MySettings} from "../types/MySettings";

export class HHeroFetch {
    constructor() {
        this._fetch = fetchRetry(fetch, {
            retries: 5,
            retryDelay: 800
        });
    }
    _fetch:any
    currentFetches=0
    requestSeq=0
    fetchQueue:any[]=[]
    updateStateFunction:any
    static _instance:HHeroFetch
    static get_instance():HHeroFetch {
        if (!HHeroFetch._instance) HHeroFetch._instance = new HHeroFetch()
        return HHeroFetch._instance
    }


    static checkFetchQueue() {
        const hf=HHeroFetch.get_instance()
        for(let i=0;i<hf.fetchQueue.length;i++) {
            const fq=hf.fetchQueue[i]
            if (hf.currentFetches<MySettings.concurrentFetchCount) {
                hf.currentFetches++
                hf._fetch(fq.url,fq._params).then((res:any) => {
                    fq._resolve(res)
                }).catch((err:any)=>{
                    fq._reject(err)
                }).finally(()=>{
                    hf.currentFetches--
                    HHeroFetch.checkFetchQueue()
                    fq._finally()
                    console.log("Done fetch "+fq.id+" ["+ hf.currentFetches + "/" + hf.fetchQueue.length + "]")
                })
                hf.fetchQueue.splice(i,1)
                console.log("Removed fetch from queue "+fq.id+" ["+ hf.currentFetches + "/" + hf.fetchQueue.length + "]")
            } else {
                break
            }
        }
        hf.updateState()
    }
    updateState() {
        if (this.updateStateFunction) {
            if (this.currentFetches>0 || this.fetchQueue.length>0) {
                this.updateStateFunction("[" + this.currentFetches + "/" + this.fetchQueue.length + "]")
            } else {
                this.updateStateFunction("")
            }
        }
    }

    static fetch(url:any,_params:any,_resolve:any,_reject:any,_finally:any) {
        const hf=HHeroFetch.get_instance()
        const id=hf.requestSeq++
        hf.fetchQueue.push({url:url,_params:_params,_resolve:_resolve,_reject:_reject,_finally:_finally,id:id})
        console.log("Added fetch to queue "+id+" ["+ hf.currentFetches + "/" + hf.fetchQueue.length + "]")
        HHeroFetch.checkFetchQueue()
        return id
    }
}