import React, {useState} from "react";
import {Box} from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";
import {useTranslation} from "react-i18next";
import {HHeroFetch} from "../utils/HHeroFetch";


interface IProps {
    message: string,
    openMenuFunction:any
}

export function HeaderBlock(props:IProps) {
    const [message2, setMessage2] = useState("");
    HHeroFetch.get_instance().updateStateFunction=setMessage2
    const { t } = useTranslation();
        return (
            <Box className={"headerBlock"}>
                <MenuIcon onClick={props.openMenuFunction}/>&lt;{t('Menu')}.&nbsp;&nbsp;
                <Box>{props.message}</Box>
                <Box>{message2}</Box>
            </Box>
        )

}